.new {
  width: 100%;
  display: flex;

  .newContainer {
    flex: 6;

    .top,
    .bottom {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 10px;
      margin: 20px;
      display: flex;
      flex-direction: inherit;

      h1 {
        color: lightgray;
        font-size: 20px;
      }

      .left {
        flex: 1;
        text-align: center;

        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          object-fit: cover;
        }
      }

      .right {
        flex: 2;

        form {
          display: block;
          flex-wrap: wrap;
          gap: 20px;
          justify-content: space-around;

          .form-inputs {
            display: flex;
            flex-wrap: wrap;
            gap: 4rem;
            justify-content: space-between;

            .formInput {
              width: 40%;
              margin-top: 1rem;

              label {
                display: flex;
                align-items: center;
                gap: 10px;

                .icon {
                  cursor: pointer;
                }
              }

              input {
                width: 100%;
                padding: 5px;
                border: none;
                border-bottom: 1px solid grey;
              }
            }
          }
          button {
            width: 150px;
            padding: 10px;
            border: none;
            background-color: teal;
            color: white;
            font-weight: bold;
            cursor: pointer;
            margin-top: 3rem;

            &:disabled {
              background-color: rgba(0, 128, 128, 0.353);
              cursor: not-allowed;
            }
          }
        }
      }
    }
  }
}
