.navbar {
  // height: 50px;
  // border-bottom: 0.5px solid rgb(231, 228, 228);
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #555;
  background-color: var(--primary);

  .wrapper {
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .search {
      display: flex;
      align-items: center;
      border: 1px solid var(--white);
      border-radius: 5px;
      padding: 3px;

      input {
        border: none;
        outline: none;
        background-color: transparent;
        color: var(--white);

        &::placeholder {
          font-size: 14px;
          color: var(--white);
        }
      }
    }

    .items {
      display: flex;
      align-items: center;

      .item {
        display: flex;
        align-items: center;
        margin-right: 20px;
        position: relative;
        text-decoration: none;
        color: var(--white);

        .icon {
          font-size: 20px;
          cursor: pointer;
        }
        .avatar {
          height: 30px;
          width: 30px;
          border-radius: 50%;
        }

        .counter {
          width: 15px;
          height: 15px;
          background-color: var(--red);
          border-radius: 50%;
          color: var(--white);
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 10px;
          font-weight: bold;
          position: absolute;
          top: -5px;
          right: -5px;
        }
      }
    }
  }
}
